import React from 'react'
import classes from './Burger.css'
import BurgerIngrediant from './BurgerIngredient/BurgerIngredient'
//ingredients is object .. and we should convert that object in array using map method

const burger = (props) => {

    let transformedIngredients = Object.keys(props.ingredients)
           .map(igKey => {
               return [...Array(props.ingredients[igKey])]
           .map((_,i)=>{
                   return <BurgerIngrediant key = {igKey+i} type = {igKey} />;
               });
           }) 
           .reduce((arr,el) => {
               return arr.concat(el)
           } , []);

           if (transformedIngredients.length === 0){
                transformedIngredients = <p>Please start adding ingredients!</p>;
           }

           //console.log(transformedIngredients)
           // .reduce () convert array of arrays into array of objects
           //arr is previouse value , el is current value 
           // props.ingredients[igKey] is value of key
           // in map(_,1) method -> _ is each element of array .. i is index of thats elemenets .. _ means thats there are not elements (there are empty spaces)
           //keys from object ingredients from state are converted into array (transformedIngrediants)
           // Array(n) method return array with n empty spaceses
    return (
        <div className={classes.Burger}>
            <BurgerIngrediant type="bread-top" />
                {transformedIngredients}
            <BurgerIngrediant type="bread-bottom" />
        </div>
    );
}

export default burger;